.App{text-align:center;width:100%;height:auto;font-family:noto-sans, sans-serif}.App::before{content:" ";position:fixed;top:0px;left:0px;height:100vh;width:100vw;background-size:cover}ul,p,h1,h2,h3,h4{padding:0px;margin:10px 0}h1{font-size:1rem;text-transform:uppercase}h2{font-family:brevia, sans-serif;text-transform:capitalize}h3{display:inline-flex;font-size:1rem}li{list-style:none}.hide-me{position:fixed;visibility:hidden}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-thumb{background:#888}html{height:100%}#root{height:100%}.two-column{display:grid;grid-template-columns:1fr 1fr;padding:10px;box-sizing:border-box}@media screen and (max-width: 600px){.two-column{grid-template-columns:1fr}}

#cover-me{position:fixed;top:0px;z-index:1;height:100%}body{position:relative;width:100%;height:100%}.enter h1{font-size:50px}@-webkit-keyframes enter{from{transform:0}to{opacity:1}}@keyframes enter{from{transform:0}to{opacity:1}}@-webkit-keyframes exit{from{opacity:1}to{opacity:0}}@keyframes exit{from{opacity:1}to{opacity:0}}.container-header{height:auto;min-height:100vh;box-sizing:border-box;margin:0;z-index:2;flex-wrap:wrap;align-items:center;padding:0px;grid-template-rows:auto 1fr auto;display:grid;position:relative}.container-header h1{margin:0px;word-wrap:break-word;text-transform:uppercase}.container-header h3{margin:0;margin-top:-10px;font-size:1rem;line-height:2.5rem;letter-spacing:1px;font-family:noto-sans, sans-serif;text-transform:uppercase}

