.top-nav{z-index:10;position:-webkit-sticky;position:sticky;top:0px;width:100%;height:100%;padding:30px 0px;box-sizing:border-box;margin:0px}.top-nav a{margin:0px 10px;text-decoration:none;text-transform:uppercase;position:relative;font-weight:600;opacity:1;height:20px}.top-nav a:hover{transition:0.2s}.top-nav .icon{text-align:left;padding-left:100px;display:flex;align-items:center;grid-gap:15px}.top-nav .icon img{width:25px;height:25px;overflow:hidden;border-radius:50%}.top-nav .nav{min-height:50px;line-height:50px;text-align:right;display:flex;justify-content:flex-end;align-items:center;flex-wrap:wrap}

.web-container{height:100%;text-align:left;box-sizing:border-box}.web-container .main-container{display:grid;grid-template-columns:300px 1fr;grid-gap:15px;max-width:1000px;margin:auto}@media screen and (max-width: 700px){.web-container .main-container{grid-template-columns:1fr}}.web-container a{color:#008aff;text-decoration:none;font-weight:600}@media screen and (max-width: 700px){.web-container .control-header{display:grid;grid-template-columns:70px 1fr;position:relative}.web-container .control-header .prev{grid-column:1;grid-row:1}.web-container .control-header .header-text{grid-row:2;grid-column:1/-1}.web-container .control-header .next{grid-column:2;grid-row:1}}.web-container .arrows{display:flex;grid-gap:15px}.web-container .arrows .act-button{opacity:1;border-radius:50%;width:30px;height:30px;display:inline-flex;align-content:center;justify-content:center;align-items:center;font-size:1.5rem;cursor:pointer;color:#5d5d5d;border:1px solid #999}.web-container .arrows .act-button:hover{border-radius:25px;transition:0.2s;background:#008aff;color:white}.web-container .arrows .inactive{background:#5d5d5d;color:white;cursor:not-allowed}.web-container .proj-image{display:grid;grid-gap:10px;grid-template-columns:repeat(auto-fill, minmax(140px, 1fr))}.web-container .proj-image span{width:auto;display:flex;max-width:200px;max-height:200px;display:flex;align-items:center;align-content:center;justify-items:center;justify-content:center;text-decoration:none;padding:10px;box-sizing:border-box;text-align:center;position:relative;cursor:pointer;border-radius:5px;border-width:1px;border-style:solid;font-size:.6rem}.web-container .proj-image span:hover{transition:0.5s;color:black;background:rgba(255,255,255,0.5)}.web-container .proj-image span img{width:100%;object-fit:contain;height:100%;border-radius:5px}.web-container .proj-image .proj-video{height:100px;position:relative}.web-container .proj-image .proj-video:before{content:"▸";width:100%;height:100%;position:absolute;font-size:5rem;line-height:5rem}.web-container .tags{display:flex;flex-wrap:wrap}.web-container .tags .tag{cursor:pointer;padding:5px;margin:5px;border-width:1px;border-style:solid}.web-container .tags .tag:hover{background:#008aff;color:white}.web-container .tags .active{background:#daa520 !important}.web-container .project-container{width:100%;box-sizing:border-box;position:relative;max-width:900px;margin:10px auto;border-radius:10px;z-index:2;padding:30px}.web-container .project-container .desc-container{margin:10px 0}.web-container .project-container .links-container a{margin:5px;text-decoration:none;color:#008aff;font-weight:600}.web-container .project-container .links-container a:hover{transition:.2s;color:#5d5d5d}.web-container .project-container .top-right{position:absolute;top:5px;right:20px;text-align:right}.web-container .project-container .top-right p{margin-bottom:10px}.web-container .project-container .top-right .gold-text{font-weight:800;color:#daa520;padding-left:10px}.web-container .project-container .top-right button{cursor:pointer;border-radius:5px;box-sizing:border-box;padding:5px 10px;outline:0px;font-weight:600;box-shadow:1px 1px 5px rgba(0,0,0,0.1)}.web-container .project-container .top-right button:hover{box-shadow:none}.modal{position:fixed;top:10%;left:50%;transform:translateY(-10%) translateX(-50%);border-radius:5px;width:100%;max-width:800px;box-shadow:0px 3px 20px rgba(0,0,0,0.2);padding:35px 15px}.modal .close{position:absolute;top:0px;right:5px;font-size:2rem;font-weight:800;cursor:pointer;z-index:99;padding:5px 10px;line-height:2rem;border-radius:50%}.modal .close-2{float:right;padding:5px;box-sizing:border-box;cursor:pointer}.modal .main-content{display:grid;overflow:hidden;max-height:800px;grid-gap:15px}.modal .main-content embed{width:100%;max-height:500px;object-fit:contain}.top{width:100%;padding:10px;box-sizing:border-box;border-bottom-style:solid;border-bottom-width:1px}.top .top-container{margin:auto;max-width:800px;width:100%;align-items:center;padding-bottom:15px}.top button{background:none;border:0px;font-size:1.5rem;color:#686868;cursor:pointer}.top .filter{display:grid;border-bottom-width:1px;border-bottom-style:solid;padding:10px;box-sizing:border-box}.top .filter p{text-transform:uppercase;font-weight:600}.top .filter .filter-section{display:grid;grid-template-columns:1fr 50px 1fr;grid-gap:10px}.top .filter .filter-section .active-tag{font-weight:600;color:#008AFF}.top .filter .filter-section button{background:none;border:0px}.top .filter span{display:flex;align-content:center;align-items:center}.top .filter select{border:0px;outline:0px;cursor:pointer}.top .filter select .option{padding:10px}

.proj-nav{position:relative;display:grid;padding-top:50px;height:100%;grid-gap:5px;grid-template-rows:repeat(auto-fill, minmax(50px, auto))}.proj-nav:before{content:' ';height:100%;border-left-width:1px;border-left-style:solid;position:absolute;left:25px}.proj-nav strong{color:#DAA520}.proj-nav p{position:relative;padding-left:15px;display:flex;align-items:center;align-content:center;cursor:pointer;margin-bottom:10px;transition:.2s;letter-spacing:1px}.proj-nav p img{height:20px;height:20px;padding-right:20px}.proj-nav p:hover{font-weight:bold;transition:.2s}@media screen and (max-width: 800px){.proj-nav{display:none}}

.art-gallery-container{display:grid;grid-template-columns:200px 1fr;margin:50px auto;max-width:900px;width:100%;justify-items:center;padding:30px;border-radius:10px;grid-gap:20px;box-sizing:border-box;min-height:600px}@media screen and (max-width: 600px){.art-gallery-container{grid-template-columns:1fr}}.art-gallery-container h1,.art-gallery-container h2{padding:0px;margin:0px}.art-gallery-container h1{font-size:1rem;text-transform:uppercase}.art-gallery-container h2{font-family:brevia, sans-serif;text-transform:capitalize}.art-gallery-container .sidebar{text-align:left;position:relative}.art-gallery-container nav{position:absolute;bottom:0px}@media screen and (max-width: 600px){.art-gallery-container nav{position:relative;text-align:center}}.art-gallery-container nav ul{line-height:2.5rem}@media screen and (max-width: 600px){.art-gallery-container nav ul{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:10px}}.art-gallery-container nav ul li{cursor:pointer}.art-gallery-container nav ul li:hover{color:#DAA520;background:none}.art-gallery-container nav ul .active-tab{padding-left:10px;color:#DAA520;font-weight:800;border-left:10px solid #DAA520}@media screen and (max-width: 600px){.art-gallery-container nav ul .active-tab{border-left:0px;border-bottom:10px solid #DAA520}}.art-gallery-container .desc{min-height:100px}.art-gallery-container .button-container{display:flex;align-items:center;justify-items:center;align-content:center;justify-content:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.art-gallery-container .button-container .circle-button{margin:10px;display:flex;padding:10px;border-radius:50%;width:20px;height:20px;align-items:center;justify-items:center;align-content:center;justify-content:center;cursor:pointer}.art-gallery-container .button-container .circle-button:hover{transition:0.2s;background:#008aff;color:white}.art-gallery-container .content-container{width:100%;overflow-x:hidden;position:relative}.art-gallery-container .main-image{position:-webkit-sticky;position:sticky;left:0px;margin-bottom:30px}.art-gallery-container .main-image img,.art-gallery-container .main-image iframe{border-radius:5px;object-fit:cover;max-width:800px;width:100%}.art-gallery-container .main-image iframe{width:100%;height:400px;border:0px;outline:0px}.art-gallery-container .gallery-image-container{display:flex;overflow-x:auto;width:100%}.art-gallery-container .gallery-image-container a:focus{border-top:3px solid white;outline:0px}.art-gallery-container .gallery-image-container img{cursor:pointer;height:100px;width:auto;object-fit:cover;margin:10px;border-radius:5px}.art-gallery-container .gallery-image-container p{display:flex;align-items:center;justify-items:center;align-content:center;justify-content:center;padding:10px;border-radius:5px;cursor:pointer;margin:15px 10px;box-sizing:border-box;min-width:100px;border:1px solid rgba(255,255,255,0.3)}.art-gallery-container .gallery-image-container p:hover{transition:.2s;background:rgba(255,255,255,0.3)}

.mode{font-size:1.5rem;position:relative;display:grid;grid-template-columns:repeat(3, 70px);grid-gap:5px;margin:auto;max-width:300px;padding:10px;box-sizing:border-box;border-radius:55px;overflow:auto;font-weight:400;font-family:great-vibes, sans-serif;align-items:center;justify-content:center}.mode span{cursor:pointer}.switch{position:relative;justify-self:center;width:60px;height:34px;border-width:1px;border-style:solid;border-radius:15px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;top:0;left:0;right:0;bottom:0;transition:0.4s;overflow:auto}.slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:#363636;transition:0.4s;border:1px solid gray}input:checked+.slider{box-shadow:0px 0px 16px rgba(122,97,62,0.2)}input:checked+.slider:before{background-color:white}input:checked+.slider:before{transform:translateX(26px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

footer{width:100%;height:auto;box-sizing:border-box;padding:10px;border-top-width:1px;border-top-style:solid;z-index:2}footer .credit{text-align:right}footer .contact-container{z-index:3;box-sizing:border-box}footer .contact-container .contact-list{display:flex;justify-content:start;align-items:center;margin-left:auto}footer .contact-container .contact-list a{display:flex;word-break:break-word;color:white;text-decoration:none;height:40px;margin:0 10px;align-items:center}footer .contact-container .contact-list a img{height:100%;max-width:25px;object-fit:contain;transition:0.2s}footer .contact-container .contact-list a:hover img{transition:0.2s;-webkit-filter:grayscale(100%) brightness(120%);filter:grayscale(100%) brightness(120%)}

