@import url("https://use.typekit.net/tcr2vad.css");
.App {
  text-align: center;
  width: 100%;
  height: auto;
  font-family: noto-sans, sans-serif;
}


.App::before {
  content: " ";
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100vh;
  width: 100vw;
  background-size: cover;
}
ul, p, h1, h2, h3, h4 {
  padding: 0px;
  margin: 10px 0;
}
h1 {
  font-size: 1rem;
  text-transform: uppercase;
}
h2 {
  font-family: brevia, sans-serif;
  text-transform: capitalize;
}
h3 {
  display: inline-flex;
  font-size: 1rem;

}
li {
  list-style: none;
}
.hide-me {
  position: fixed;
  visibility: hidden;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: #888; 
}

html {
  height: 100%;
}

#root {
  height: 100%;
}

.two-column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
  box-sizing: border-box;
  @media screen and (max-width: 600px) {
    grid-template-columns: 1fr;

  }
} 
